ling God had placed in his heart and on his life was confirmed. He began to understand that the situation of loss these people were in was not any different from the need all human beings have. Although our material situations are different, our spiritual situation is the same. The beginnings were not easy, we met every week for a while and also invited friends from church who had the same visin of being able to help others. We visited teh park continuously. To save on costs, as there were little rescources, Ronald asked his Mom to begin preparing the food. In this way Ronald continued with this activity every Sunday without receiving any salary. The call was strong in his heart that he decided to quit his job knowing the iplications that this had. But God, through Valerie Edwards(Founder of Fort he Love of Mateo) provided his salary for a year. She did everything possible to legally register DBO ministries under Guatemalan law as an organization in full capacity in the country. This lit a fire for Ronald to continue with Gods calling on his life. \n                ")]),_c('blockquote',{staticClass:"blockquote shadow p-3",staticStyle:{"border-left":"solid 2px #222","font-size":"1rem"}},[_c('p',{staticClass:" text-center"},[_vm._v("\n                        A phrase that for our founder marked this time in his life and continues to do so is:  "),_c('br'),_c('i',{staticClass:"bold gold"},[_vm._v("The best medicine for a broken heart is to serve God and your neighbor.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row m-5"},[_c('div',{staticClass:"col-12 text-dark m-3"},[_c('h1',{staticClass:" text-secondary mt-3 "},[_vm._v("Declaration of Faith")]),_c('h1',{staticClass:"bold display-4"},[_vm._v("DBO MINISTRIES")])]),_c('div',{staticClass:"mt-4 col-md-3 text-center"},[_c('div',{staticClass:"m-1 shadow rounded p-4 zoom2"},[_c('img',{attrs:{"src":"images/bible.png","alt":"bible","height":"120"}}),_